import React, {useState, useEffect} from 'react';
import styles from './style.module.css';
import cn from 'classnames';
import Row from "react-bootstrap/Row";
import Container from "react-bootstrap/Container";
import Profile from "./contents/Profile";
import Wrapper from "../common/core/Wrapper";
import Button from "../common/core/Button";
import Image from "react-bootstrap/Image";
import {Form} from 'informed';
import logoImagePath from "./images/logo_sm.png";
import SignFooter from "../common/parts/SignFooter";
import Company from "./contents/Company";
import Business from "./contents/Business";
import withOnImagesLoadedRender from "../common/HOCs/withOnImagesLoadedRender";
import globalStyles from '../common/global.module.css';

const WIDTH = 700;
const BAR_UNIT_WIDTH = 70;
const UNIT_ROUND = 30;

const STEPS = [
  {
    name: 'Profile',
    content: <Profile/>,
  },{
    name: 'Company',
    content: <Company/>,
  },{
    name: 'Business',
    content: <Business/>,
  },
];

const LAST_STEP_INDEX = STEPS.length-1;

//
const FINAL_STEP_CONTENT = <div className={'mt-3'}>Congratulations! Check your email...</div>;
const FINAL_STEP_INDEX = LAST_STEP_INDEX+1;

//
const register = (data) => console.log(data);


const SignUp = () => {
  const [activeStepIndex, setActiveStepIndex] = useState(0);

  // smth better??..
  const [data, setData] = useState([]);
  useEffect(() => {
    if (activeStepIndex === FINAL_STEP_INDEX) register(data);
  });

  const nextStep = () => {
    setActiveStepIndex(activeStepIndex+1);
    window.scrollTo({top: 0});
  };

  const onFormSubmit = (formState) => {
    //
    setData([...data, Object.assign({}, formState)]);
    nextStep();
  };

  const forms = STEPS.map((step, index) => (
    <Form
      onSubmit={onFormSubmit}
      className={cn(styles.form, index !== activeStepIndex && globalStyles.hidden)}
    >
      {step.content}
      <Row className={styles.buttonContainer}>
        <Button
          className={styles.button}
          type={'submit'}
          title={activeStepIndex === LAST_STEP_INDEX ? 'register' : 'next step'}
        />
      </Row>
    </Form>
  ));

  const content = activeStepIndex !== FINAL_STEP_INDEX ? forms : FINAL_STEP_CONTENT;

  return (
    <Wrapper className={cn(styles.wrapper)}>
      <Row className={styles.hat}>
        <Image height={40} className={styles.logoImage} src={logoImagePath}/>
      </Row>
      <Container className={styles.main} style={{width: WIDTH}}>
        <Row className={styles.header}>
          <span>Registration form</span>
        </Row>

        <ProgressBar stepIndex={activeStepIndex}/>

        {content}

        <SignFooter className={styles.footer}/>

      </Container>
    </Wrapper>
  );
};

const ProgressBar = ({stepIndex}) => (
  <Row className={styles.barContainer} style={{width: WIDTH+BAR_UNIT_WIDTH}}>
    <Row
      className={styles.barLine}
      style={{
        top: UNIT_ROUND/2,
        left: BAR_UNIT_WIDTH/2,
        width: WIDTH
      }}
    >{}</Row>
    {STEPS.map((step, i) => (
      <Unit key={step.name} text={step.name} complete={i < stepIndex} current={i === stepIndex}/>
    ))}
  </Row>
);

const Unit = ({text, complete, current}) => (
  <div className={styles.barUnitContainer} style={{width: BAR_UNIT_WIDTH}}>
    <div
      className={cn(styles.barUnitRound, current && styles.current)}
      style={{width: UNIT_ROUND, height: UNIT_ROUND}}
    >
      {complete && <Check/>}
    </div>
    <div className={styles.barUnitText}>{text}</div>
  </div>
);

const Check = () => (
  <div
    className={styles.check}
    style={{
      width: UNIT_ROUND / 2,
      height: UNIT_ROUND / 3,
      marginTop: - UNIT_ROUND / 5,
    }}
  />
);

export default withOnImagesLoadedRender(SignUp);