import {useState, useEffect} from 'react';
import {createAddItemFunc, createEditItemFunc, createDeleteItemFunc} from "./DataContext";

// DEV DATA IMITATION
const getProductsData = () => {
  const productItemShape = {
    code: 0,
    name: 'Lorem ipsum',
    price: '$120',
    measure: 'Lorem',
    producer: 'Lorem ipsum dolor',
    description: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit',
    delivery: '5 days',
  };

  return [...Array(100)]
    .fill(productItemShape)
    .map(({code, ...rest}, i) => ({code: i, ...rest}));
};
//
const getServicesData = () => {
  const serviceItemShape = {
    code: 0,
    name: 'Lorem ipsum',
    price: '$5000',
    measure: 'Lorem',
    description: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit',
    delivery: '10 days',
  };

  return [...Array(100)]
    .fill(serviceItemShape)
    .map(({code, ...rest}, i) => ({code: i, ...rest}));
};

export default function useDataContextValue() {
  const [dataContextValue, setDataContextValue] = useState({});

  const addProduct = createAddItemFunc('productsData', setDataContextValue);
  const editProduct = createEditItemFunc('productsData', setDataContextValue);
  const deleteProduct = createDeleteItemFunc('productsData', setDataContextValue);

  const addService = createAddItemFunc('servicesData', setDataContextValue);
  const editService = createEditItemFunc('servicesData', setDataContextValue);
  const deleteService = createDeleteItemFunc('servicesData', setDataContextValue);

  useEffect(() => {
    const productsData = getProductsData();
    const servicesData = getServicesData();
    const value = {
      productsData,
      addProduct,
      editProduct,
      deleteProduct,

      //
      servicesData,
      addService,
      editService,
      deleteService,
    };
    setDataContextValue(value);
  }, []);

  return dataContextValue;
}