import useMouseDown from "../../hooks/useMouseDown";
import styles from './style.module.css';
import cn from 'classnames';
import React from "react";

export default function TouchableOpacity({children, className, ...otherProps}) {
  const [pressing, onMouseDown] = useMouseDown();
  return (
    <div
      className={cn(styles.element, pressing && styles.pressing, className)}
      onMouseDown={onMouseDown}
      {...otherProps}
    >
      {children}
    </div>
  );
}