import React, {useState, useEffect} from "react";
import Wrapper from "../common/core/Wrapper";
import Menu from "./Menu";
import styles from './style.module.css';
import Header from "./Header";
import withOnImagesLoadedRender from "../common/HOCs/withOnImagesLoadedRender";
import Templates from "./contents/Templates";
import avatarSrc from './images/adam.jpg';
import EditUserInfo from "./UserSettings";
import Products from "./contents/Products";
import Services from "./contents/Services";
import cn from "classnames";
import globalStyles from "../common/global.module.css";
import DataContext from './DATA_CONTROL/DataContext';
import useDataContextValue from "./DATA_CONTROL/useDataContextValue";

// DEV
const user = {name: 'Adam', surname: 'Smith', avatarSrc};

const TABS = [
  {
    name: 'Templates',
    content: <Templates/>,
  },{
    name: 'Documents',
    content: null,
  },{
    name: 'Products',
    content: <Products/>,
  },{
    name: 'Services',
    content: <Services/>,
  },{
    name: 'My business',
    content: null,
  },{
    name: 'Partners',
    content: null,
  },{
    name: 'Competitors',
    content: null,
  },
];

const HEADER_HEIGHT = 60;
const LEFT_SIDE_WIDTH = 250;


function Dashboard() {
  // data
  const dataContextValue = useDataContextValue();

  // layout
  const [activeTabIndex, setActiveTabIndexState] = useState(0);
  const [showingUserInfo, showUserInfo] = useState(false);

  const setActiveTabIndex = (tab) => {
    setActiveTabIndexState(tab);
    showUserInfo(false);
  };

  // TODO abstract it! (component with render condition)
  const tabContents = TABS
    .map(({content}, index) => (
    <div className={cn(styles.tabContainer, (index !== activeTabIndex || showingUserInfo) && globalStyles.hidden)}>
      {content}
    </div>
  ));

  const userInfo = (
    <div className={cn(styles.tabContainer, !showingUserInfo && globalStyles.hidden)}>
      <EditUserInfo showUserInfo={showUserInfo}/>
    </div>
  );

  return (
    <DataContext.Provider value={dataContextValue}>
    <Wrapper className={styles.wrapper}>
      <Header height={HEADER_HEIGHT} leftSideWidth={LEFT_SIDE_WIDTH}/>
      <div className={styles.main}>
        <Menu
          user={user}
          items={TABS}
          activeTabIndex={activeTabIndex}
          setTabIndex={setActiveTabIndex}
          showUserInfo={showUserInfo}
          width={LEFT_SIDE_WIDTH}
        />
          {[tabContents, userInfo]}
      </div>
    </Wrapper>
    </DataContext.Provider>
  );
}

export default withOnImagesLoadedRender(Dashboard);
// export default Dashboard;