import React from 'react';

export const createDeleteItemFunc = (field, resolver) => {
  return (code) => resolver(({[field]: source, ...rest}) => ({
    [field]: source.filter(item => item.code !== code),
    ...rest
  }));
};

export const createEditItemFunc = (field, resolver) => {
  return (code, buffer) => resolver(({[field]: source, ...rest}) => ({
    [field]: source.map(
      item => item.code === code ? buffer : item
    ),
    ...rest
  }));
};

// TODO ask :)
export const createAddItemFunc = (field, resolver) => {
  return (item) => resolver(({[field]: source, ...rest}) => ({
    [field]: [
      ...source,
      Object.assign({...item}, {code: source[source.length-1].code + 1}),
    ],
    ...rest
  }));
};

const DataContext = React.createContext({
  productsData: null,
  addProduct: () => {},
  editProduct: () => {},
  deleteProduct: () => {},

  servicesData: null,
  addService: () => {},
  editService: () => {},
  deleteService: () => {},
});

export default DataContext;