import cn from "classnames";
import globalStyles from "../global.module.css";
import React from "react";

const Elect = ({children, index, className='', defaultComponent=null}) => (
  index !== null ? children.map((item, i) => (
    <div className={cn(i !== index ? globalStyles.hidden : className)}>
      {item}
    </div>
  )) : defaultComponent
);

export default Elect;