import React, {useState} from 'react';
import styles from './style.module.css';
import cn from 'classnames';
import TouchableOpacity from "../TouchableOpacity";

const Button = ({title, onClick=()=>{}, className, height, width, color, thin, fontSize='inherit'}) => {
  title = typeof title === 'string' ? title.toUpperCase() : title;

  return (
    <TouchableOpacity className={styles.container} style={{height, width}}>
      <button
        className={cn(styles.button, className)}
        onClick={onClick}
        style={{
          backgroundColor: color,
          fontWeight: thin ? '400' : '700',
          fontSize
        }}
      >
        {title}
      </button>
    </TouchableOpacity>
  );
};

export default Button;