import React, {useContext} from "react";
import EditableTable from '../../../common/parts/EditableTable';
import "react-table/react-table.css";
import DataContext from "../../DATA_CONTROL/DataContext";

const columns = [{
  Header: 'CODE#',
  accessor: 'code',
  width: 65,
},{
  Header: 'NAME',
  accessor: 'name',
  width: 120,
},{
  Header: 'PRICE',
  accessor: 'price',
  width: 80,
},{
  Header: 'U.M.',
  accessor: 'measure',
  width: 80,
},{
  Header: 'PRODUCER',
  accessor: 'producer',
  width: 120,
},{
  Header: 'GENERAL DESCRIPTION',
  accessor: 'description',
  minWidth: 250,
},{
  Header: 'DELIVERY',
  accessor: 'delivery',
  width: 85,
}];

function ProductsTab () {
  const dataContext = useContext(DataContext);

  return (
    <EditableTable
      data={dataContext.productsData}
      deleteItem={dataContext.deleteProduct}
      editItem={dataContext.editProduct}
      columns={columns}
    />
  );
}


export default ProductsTab;