import React from "react";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Button from '../../../common/core/Button';
import styles from './style.module.css';
import Truncate from '../../../common/core/Truncate';
import cn from 'classnames';
import clipImagePath from '../../images/icons/templates/clip_red.png';
import clipboardBlueImagePath from '../../images/icons/templates/clipboard_blue.png';
import Image from "react-bootstrap/Image";
import TouchableOpacity from "../../../common/core/TouchableOpacity";
import useClientRect from "../../../common/hooks/useClientRect";

const itemShape = {date: '20.06.20', text: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit.'};

//DEV
const items = [...Array(7)].fill(itemShape);
const documents = items.slice(3);

const BOARD_ITEM_ICON_SIZE = 25;


function Templates() {
  const boardItems = items.map((item, i) => <BoardItem info={item}/>);
  const listItems = documents.map((doc, i) => <ListItem info={doc}/>);

  return (
    <div className={styles.container}>

      <div className={styles.main}>
        <Row className={styles.boardHeader}>
          <Col className={styles.headerName}>FREQUENTLY USED</Col>
          <div className={styles.seeAll}>SEE ALL</div>
          <div className={styles.createNew}>
            <Button thin className={styles.createNewButton} title={'create new'} height={28} width={130}/>
          </div>
        </Row>
        <Row className={styles.board}>
          {boardItems}
        </Row>
      </div>

      <div className={styles.latestDocuments}>
        <Row className={styles.listHeader}>
          <Col className={styles.headerName}>LATEST DOCUMENTS</Col>
          <div className={styles.seeAll}>SEE ALL</div>
        </Row>
        <Row>
          {listItems}
        </Row>
      </div>

    </div>
  );
}

const BoardItem = ({info}) => (
  <Col className={'col-4'}>
    <TouchableOpacity>
      <div className={styles.boardItem}>
        <Row className={styles.boardItemDate}>{info.date}</Row>
        <Row className={styles.boardItemText}>
          <Truncate lines={3} ellipsis={'...'}>
            {info.text}
          </Truncate>
        </Row>
        <div className={styles.boardItemIcon}>
          {/*dev*/}<Image height={BOARD_ITEM_ICON_SIZE} src={clipImagePath}/>
        </div>
      </div>
    </TouchableOpacity>
  </Col>
);

function ListItem ({info}) {
  return (
    <Col className={'col-12'}>
      <TouchableOpacity>
        <div className={styles.listItem}>
          <Col className={cn(styles.listItemIconContainer, 'col-2')}>
            {/*dev*/}<Image height={25} src={clipboardBlueImagePath}/>
          </Col>
          <Col className={'col-10 pr-3'}>
            <Row className={styles.boardItemDate}>{info.date}</Row>
            <Row className={styles.listItemText}>
              <Truncate
                lines={2}
                ellipsis={'...'}
              >
                {info.text}
              </Truncate>
            </Row>
          </Col>
        </div>
      </TouchableOpacity>
    </Col>
  );
};

export default Templates;